import React from 'react'
import $confirm from '../functions/$confirm'

const msg = 
`Wallet generated successfully, you will now be shown 
the private key using which you can unlock your wallet. 

<br />
<br />

Please understand that the private key can be used to access
all the funds in your wallet, 

<strong>make sure that only YOU have the private key for your wallet 
and that the private key is kept safe and secret.</strong>

<br />
<br />

You can click YES to proceed, or click NO to stop.`

const CreateWallet = () => (
	<button onClick={createNewWallet} 
		type="button" className="btn btn-success btn-lg create-new-wallet">
		<span className="fa fa-plus-circle"></span>&nbsp;&nbsp;CREATE NEW WALLET
	</button>
)

async function createNewWallet() {
	let wallet = window.Wallet.generate()
	
	let privKey = wallet.getPrivateKeyString()
	let address = wallet.getChecksumAddressString()

	let isSure = await $confirm(msg, undefined, true)

	if (!isSure) return

	$confirm(`Wallet Address: <code>${address}</code><br /><br />
		Private Key: <code>${privKey}</code><br /><br />
		<strong>Please copy the above information and save them somewhere safe and secret,
		remember that all the funds in the wallet can be accessed using the private key.</strong>
		`,'Keep your private key safe and secret', true, 
		{	
			useBootstrap: false,
			boxWidth: '60%',
			buttons: {
				'I\'VE SAVED MY PRIVATE KEY': {
					btnClass: 'btn btn-danger',
					action: function() {}
				}
			}
		})
}

export default CreateWallet
