export default function showBalance({state, onChange}) {
	let {coinbase, tokenContract, saleContract} = window
	if (!tokenContract) return;
	tokenContract.methods.balanceOf(coinbase).call().then(res => {
		onChange({tokenBalance: res})
	})

	saleContract.methods.price().call().then(res => {
		window.price = Number(res)
		onChange({salePrice: Number(res)})
	})

	window.web3.eth.getBalance(coinbase).then(res => {
		onChange({ethBalance: res})
	})
}
