export default function $confirm(content,
  title="Are you sure?", isDanger=false, config={}) {
  content = `<div style="word-wrap:break-word;max-width:100%;">
    ${content}
  </div>`
  return new Promise((resolve, reject) => {
    window.$.confirm({title, content,
      useBootstrap: typeof config.useBootstrap !== 'undefined' ?config.useBootstrap:true,
      boxWidth: config.boxWidth||"100%",
      animateFromElement:false,
      buttons:config.buttons || ({
        YES: {
          btnClass:`btn ${isDanger?"btn-danger":
          "btn-success green"}`,
          action:() => resolve(true)
        },
        NO: () => resolve(false)
      })
    })
  })
}
