import showBalance from './show-balance'

export default async function unlockWallet({state, onChange}, useMetamask=false) {

	if (useMetamask && typeof window.web3 !== 'undefined') {
		if (window.ethereum) {
			try {
				await window.ethereum.enable()
			} catch (e) {
				return window.alertify.error('MetaMask connect request rejected!')
			}
		}
		window.web3 = new window.Web3(window.web3.currentProvider)
		onChange({useMetamask: true})
	} else {
		try {window.Wallet.fromPrivateKey(state.privKey).getChecksumAddressString()}
		catch (e) {return window.alertify.error('Invalid Private Key!')}
		window.web3 = new window.Web3(`https://${
			window.config.network}.infura.io/v3/${window.config.infuraKey}`)
	}

	let { alertify, web3, Wallet, ABI, config } = window;


	let {tokenAddress, saleAddress} = config

	let coinbase 
	try {
		coinbase = useMetamask?(await web3.eth.getCoinbase())
		:Wallet.fromPrivateKey(state.privKey).getChecksumAddressString()
	} catch (e) {
		return alertify.error('Invalid Private Key!')
	}
	let tokenContract = new web3.eth.Contract(ABI, tokenAddress, {from: coinbase})
	let saleContract = new web3.eth.Contract(ABI, saleAddress, {from: coinbase})

	window.tokenContract = tokenContract
	window.saleContract = saleContract
	window.coinbase = coinbase
	onChange({coinbase, tokenContract, saleContract, walletUnlocked: true})

	alertify.success('Wallet unlocked!')

	showBalance({state, onChange})
	setInterval(() => showBalance({state, onChange}), 3000)
}
