import $confirm from './$confirm'


export default function sendTokens(tokenAddress, privKey, _to, _amount, {gasPriceGwei, gasLimit, chainId}={}, cb) {
  
  if (window.App.state.useMetamask) {
    return sendTokensFromMetamask(tokenAddress, _to, _amount, cb)
  }  

  return new Promise(async (resolve, reject) => {
    let {ABI, config, Wallet, web3, BN, Tx, Buffer} = window

    console.log(_to)
    if (!web3.utils.isAddress(_to)) return reject(`Invalid To Address!`)

    let _from = Wallet.fromPrivateKey(privKey).getChecksumAddressString()

    let count = await web3.eth.getTransactionCount(_from)

    console.log(`num transactions so far: ${count}`);

    let contract = new web3.eth.Contract(ABI, tokenAddress, {from: _from})

    let balance = await contract.methods.balanceOf(_from).call()
    console.log(`Balance before send: ${balance} \n------------------------`)

    if (new BN(_amount).gt(balance)) return reject("Insufficient Balance")

    gasPriceGwei = gasPriceGwei||3
    gasLimit = gasLimit || 3e6
    chainId = chainId || 4

    let rawTransaction = {
      from: _from,
      nonce: "0x"+count.toString(16),
      gasPrice: web3.utils.toHex(gasPriceGwei*1e9),
      gasLimit: web3.utils.toHex(gasLimit),
      to: tokenAddress,
      value: '0x0',
      data: contract.methods.transfer(_to, _amount).encodeABI(),
      chainId
    }

    console.log(`Raw of Transaction: \n${JSON.stringify(rawTransaction, null, '\t')}\n------------------------`);

    privKey = new Buffer(privKey, 'hex')
    let tx = new Tx(rawTransaction)
    tx.sign(privKey)

    let serializedTx = tx.serialize()
    console.log(`Attempting to send signed tx:  ${serializedTx.toString('hex')}\n------------------------`);

    let isSure = await $confirm(`Are you sure you want to send <strong>${new BN(_amount).div(1e18).toString(10)} ${config.tokenSymbol}</strong> to ${_to}`,undefined, true)

    if (!isSure) return reject(`Denied by user.`)

    web3.eth.sendSignedTransaction('0x'+serializedTx.toString('hex'), cb)
      .then(resolve)
      .catch(reject)

    // let receipt = await web3.eth.sendSignedTransaction('0x'+serializedTx.toString('hex'), cb)

    // console.log(`Receipt info: \n${JSON.stringify(receipt, null, '\t')}\n------------------------`);

    // The balance may not be updated yet, but let's check
    // balance = await contract.methods.balanceOf(_from).call();
    // console.log(`Balance after send: ${balance}`);

    // return resolve(receipt) 
  })
}


function sendTokensFromMetamask(address, _to, _amount, cb) {
  let {web3, ABI, $} = window
  return new Promise(async (resolve, reject) => {
    let contract = new web3.eth.Contract(ABI, address, {from: await web3.eth.getCoinbase()})
    let m = $.alert('Please go to MetaMask to confirm the transaction')
    contract
      .methods
      .transfer(_to, _amount)
      .send({gasPrice: 1e10}, (err, txHash) => {
        m.close()
        cb(err, txHash)
      })
      .then(resolve)
      .catch(reject)
      .finally(() => m && m.close())
  })
}
