import React, { Component } from 'react';


import unlockWallet from './functions/unlock-wallet'
import sendTokens from './functions/send-tokens'
import sendETH from './functions/send-eth'

import CreateWallet from './components/create-wallet'


import './App.css';
let { config, alertify, $, BN } = window;



let __html = `<h3>Buy ${config.tokenSymbol}</h3>
<div style="max-width:95%; text-align:center;">
<center><input class="form-control" autofocus style="border:1px solid rgba(0,0,0,.2);display:inline-block;margin-left:3px;" type="text" id="noOfEthToSendInput" placeholder="Number of ETH to send" /></center><br />
<div id="noOfTokens" style="text-align:left">0 ${config.tokenSymbol}</div>
<button class="btn btn-danger btn-lg" style="outline:none;border:1px solid rgba(0,0,0,.2); cursor:pointer;" id="prompt-buy-tokens-button">BUY</button>
</div><br />
<h3>Wanna use another wallet to Buy ${config.tokenSymbol}? Here's what you do.</h3>
 <li><strong>CONTRIBUTION ADDRESS:</strong> <h2><code>${config.saleAddress}</code></h2></li>
 <li>Gas Limit: 200,000 or more</li>
 <li>To Buy ${config.tokenSymbol}, send ETH to <strong>contribution address</strong> from <strong><a rel="noreferrer noopener" href="https://myetherwallet.com" target="_blank">myetherwallet</a></strong>   (or some ERC20 compatible wallet)</li> 
 <li><strong>DO NOT SEND ETH FROM ANY EXCHANGE WALLET, use only ERC20 compatible wallet like myetherwallet</li><li> YOUR TOKENS WILL BE LOST IF YOU SEND FROM EXCHANGE WALLET</strong></li>
 <li>To watch the tokens in your wallet, use this info:
   <ul><li>Contract Address:  <code>${config.tokenAddress}</code></li>
   <li>Symbol: ${config.tokenSymbol}</li>
   <li>Decimals: ${config.tokenDecimals}</li>
 </ul>
 </li>
<li>For info about how to make and use a wallet visit <a rel="noreferrer noopener" href="https://medium.com/the-mission/ico-101-how-to-participate-in-an-ico-made-with-ethereum-cf57516183f6" target="_blank"><strong>How to Participate in ICO</strong></a>
</li></ul>`



export default class App extends Component {

  constructor (props) {
    super(props)
    this.state = {
      walletUnlocked: false,
      privKey: '',
      coinbase: '',
      tokenContract: null,
      saleContract: null,
      salePrice: null, 
      toAddress: '',
      toAmount: '',
      tokenBalance: '',
      ethBalance: '',

      sendTokensClicked: false,
      sendEthClicked: false,

      currentTx: {
        status: '',
        txHash: '' 
      },

      metamaskPresent: null,
      canUnlock: false,
      useMetamask: false,

      failedTxns: {}
    }

    window.setState = obj => this.setState(obj)
    window.state = this.state
    window.App = this
  }

  componentDidMount() {
    window.addEventListener('load', () => {
      
      if (typeof window.web3 !== 'undefined') {
        this.setState({metamaskPresent: true, canUnlock: true})
      } else {
        this.setState({metamaskPresent: false, canUnlock: true})
      }

    })
  }


  render() {
    return (
      !this.state.walletUnlocked?
      <div className="container wallet">
        <br />
        <h1 className="text-center main-heading">
          <a href="http://www.crowdnext.com"><img 
            alt="Crowdnext Logo"
            style={{display: 'inline-block', marginTop: '-5px'}} 
            src="./crowdnext.jpg" 
            height="50" 
            width="50" /></a> {config.tokenName} WEB WALLET</h1>
        <h3>UNLOCK YOUR WALLET</h3>
        <br />
        <form onSubmit={e => {
          e.preventDefault()
          unlockWallet({state: this.state, onChange: obj => this.setState(obj)})
        }}>
          <input
            value={this.state.privKey}
            onChange={e => {
              let privKey = e.target.value
              if (privKey.startsWith('0x')) privKey = privKey.slice(2)
              this.setState({privKey})
            }} 
            autoFocus 
            type="text" 
            className="form-control input-lg" 
            placeholder="Private Key" />
          <br />
          <button disabled={!this.state.canUnlock} 
            type="submit" className="btn btn-lg btn-primary">UNLOCK WALLET</button>
        </form>
        <br />
        <div style={{fontSize: '4rem', textAlign: 'center'}}>OR</div>
        <br />
        <center><a onClick={e => {
          if (!this.state.metamaskPresent) return;

          e.preventDefault()
          unlockWallet({state: this.state, onChange: obj => this.setState(obj)}, true)
        }}
        disabled={!this.state.canUnlock}
        rel="noreferrer noopener"
        target="_blank" 
        href="https://metamask.io" 
        className="btn btn-success btn-lg connect-metamask">
          <img alt="MetaMask icon" src="./metamask.png" height="30" width='30' /> CONNECT METAMASK
        </a>&nbsp;&nbsp;&nbsp;&nbsp;
        <CreateWallet />
        </center>

      </div>
      :
      <div className="container wallet">
          <br />
          <h1 className="text-center main-heading">
            <a href="http://www.crowdnext.com"><img 
            alt="Crowdnext Logo"
            style={{display: 'inline-block', marginTop: '-5px'}} 
            src="./crowdnext.jpg" 
            height="50" 
            width="50" /></a> {config.tokenName} WEB WALLET</h1>

          <h3>MY WALLET ADDRESS: <span><a title="View account on etherscan" rel="noreferrer noopener" target="_blank" 
          href={`https://${config.network === 'rinkeby'? 'rinkeby.':''}etherscan.io/address/${this.state.coinbase}`}><code id="wallet-address">{this.state.coinbase}</code></a></span></h3>

          <div style={{minHeight: '8px'}}></div>

          <div className="row">
            <div className="col-xs-6">{config.tokenSymbol} BALANCE: <br /><strong className="bal-strong"><span id="token-balance">{(this.state.tokenBalance!==''&&!isNaN(1*this.state.tokenBalance)) ? new BN (this.state.tokenBalance).div(10**config.tokenDecimals).toFormat():''}</span> {config.tokenSymbol}</strong></div>
            <div className="col-xs-6">ETH BALANCE: <br /><strong className="bal-strong"><span id="eth-balance">{(this.state.ethBalance!==''&&!isNaN(1*this.state.ethBalance)) ? new BN(this.state.ethBalance).div(1e18).toFormat() : ''}</span> ETH</strong></div>
          </div>

          <br />
          
          <input spellCheck={false} value={this.state.toAddress}
          onChange={e => this.setState({toAddress: e.target.value.trim()})} id="to-address" className="form-control input-lg" type='text' placeholder="Send to Address" /><br />
          <input 
            value={this.state.toAmount}
            onChange={e => {
              let toAmount = e.target.value.trim()
              if (!isNaN(Number(toAmount)))
                this.setState({toAmount})
            }}
            id="to-amount" 
            className="form-control input-lg" 
            type='text' 
            placeholder="Amount" />

          <br />

          <div className="row">
            <div className="col-xs-6">
              <button onClick={e => {
                let _amt = (this.state.toAmount)
                if (isNaN(Number(_amt)) || !_amt.trim()||Number(_amt)<=0) return alertify.error('Invalid Amount')

                this.setState({sendTokensClicked: true})
                let HASH
                let cb = (err, txHash) => {
                  console.log(err, txHash)
                  if (err) {
                    this.setState({sendTokensClicked: false})
                    return;
                  }
                  let currentTx = {txHash, status: 'pending'}
                  this.setState({sendTokensClicked: false, currentTx})
                  HASH = txHash
                }

                
                let toAmount = new BN(_amt).times(10**config.tokenDecimals)

                sendTokens(
                  config.tokenAddress, 
                  this.state.privKey, 
                  this.state.toAddress,
                  toAmount,
                  {
                    gasPriceGwei: 10,
                    gasLimit: 3e6,
                    chainId: config.network==='rinkeby'?4:1
                  },
                  cb
                )
                .catch(err => {
                  alertify.error(String(err))
                  this.setState({sendTokensClicked: false})
                  if (HASH) {
                    let {failedTxns} = this.state
                    failedTxns[HASH] = true
                    this.setState({failedTxns})
                  }
                })
                .then(receipt => {
                  console.log(receipt)
                  if (!receipt) return;
                  if (receipt.status === true) {
                    let {currentTx} = this.state
                    if (currentTx.txHash !== receipt.transactionHash) return;

                    currentTx.status = 'confirmed'
                    this.setState({currentTx})
                    alertify.success("Transaction Confirmed!")
                  }
                })


              }} id="send-tokens" disabled={this.state.sendTokensClicked} className="btn btn-primary btn-lg btn-block">SEND {config.tokenSymbol}</button>
            </div>
            <div className="col-xs-6">
              <button onClick={e => {
                let toAmount = (this.state.toAmount)

                if (isNaN(Number(toAmount)) || !toAmount.trim() || Number(toAmount) <= 0) return alertify.error('Invalid Amount')



                this.setState({sendEthClicked: true})
                let HASH;
                let cb = (err, txHash) => {
                  console.log(err, txHash)
                  if (err) {
                    this.setState({sendEthClicked: false})
                    // return alertify.error(err)
                    return;
                  }
                  let currentTx = {txHash, status: 'pending'}
            
                  this.setState({sendEthClicked: false, currentTx})
                  HASH = txHash
                }

                
                sendETH(
                  this.state.privKey,
                  this.state.toAddress,
                  new BN(toAmount).times(1e18),
                  {
                    gasPriceGwei: 10,
                    gasLimit: 3e6,
                    chainId: config.network==='rinkeby'?4:1
                  },
                  cb 
                ).catch((err, receipt) => {
                  alertify.error(String(err))
                  if (HASH) {
                    let failedTxns = this.state.failedTxns
                    failedTxns[HASH] = true
                    this.setState({failedTxns})
                  }
                  this.setState({sendEthClicked: false})
                })
                .then(receipt => {
                  console.log(receipt)
                  if (!receipt) return;
                  if (receipt.status === true) {
                    let {currentTx} = this.state
                    if (currentTx.txHash !== receipt.transactionHash) return;

                    currentTx.status = 'confirmed'
                    this.setState({currentTx})
                    alertify.success("Transaction Confirmed!")
                  }
                })

              }} id="send-eth" disabled={this.state.sendEthClicked} className="btn btn-primary btn-lg btn-block">SEND ETH</button>
            </div>
          </div>
          <br />
          <div className="row">
            <div className="col-xs-6">
              <button onClick={e => {
                let dialog;
                let content = $(`<div>${__html}</div>`)
                let $buyButton = content.find('#prompt-buy-tokens-button')

                $buyButton.click(e => {
                  let val = content.find('#noOfEthToSendInput').val().trim()
                  if (isNaN(Number(val)) || Number(val) <= 0) return alertify.error('Invalid Amount!')
                  let HASH
                  let cb = (err, txHash) => {
                    console.log(err, txHash)
                    $buyButton[0].disabled = false;
                    if (err) {
                      return;
                    }
                    let currentTx = {txHash, status: 'pending'}
                    this.setState({currentTx})
                    dialog && dialog.close()
                    HASH = txHash
                  }
                  $buyButton[0].disabled = true

                  sendETH(this.state.privKey, config.saleAddress, new BN(val).times(1e18),
                  {
                    gasPriceGwei: 10,
                    gasLimit: 3e6,
                    chainId: config.network === 'rinkeby'?4:1
                  }, 
                  cb).catch(err => {
                    alertify.error(String(err))
                    $buyButton[0].disabled = false
                    if (HASH) {
                      let {failedTxns} = this.state
                      failedTxns[HASH] = true
                      this.setState({failedTxns})                      
                    }
                  }).then(receipt => {
                    if (!receipt) return;
                    if (receipt.status === true) {
                      let {currentTx} = this.state
                      if (currentTx.txHash !== receipt.transactionHash) return;
                      currentTx.status = 'confirmed'
                      this.setState({currentTx})
                      alertify.success("Transaction Confirmed!")
                    }
                  })

                })
                content.find('#noOfEthToSendInput').keyup(showNoOfTokens)
                dialog = $.confirm({
                  title: "Contribute With ETH",
                  content,
                  useBootstrap: false,
                  boxWidth: '60%'
                })
              }} id="buy-tokens" className="btn btn-primary btn-lg btn-block">BUY {config.tokenSymbol}</button>
            </div>
            <div className="col-xs-6">
              <a href={`https://${config.network === 'rinkeby'?'rinkeby.':''}etherscan.io/token/${config.tokenAddress}`} target="_blank" rel="noreferrer noopener" className="btn btn-primary btn-lg btn-block">VERIFIED ETHERSCAN</a>
            </div>
          </div>
          <br />

          {this.state.currentTx.status && <div>
            <hr />
            <strong style={{
              color: this.state.failedTxns[this.state.currentTx.txHash]?'red'
                    :this.state.currentTx.status === 'confirmed'?'green':'orange',
              fontSize: '2rem'
            }}>
            <span className={`fa ${this.state.failedTxns[this.state.currentTx.txHash]?
              "fa-times"
              :this.state.currentTx.status === 'pending'?
            "fa-spinner fa-spin":"fa-check"}`}></span>
            &nbsp;
            Transaction {this.state.failedTxns[this.state.currentTx.txHash]?"failed":this.state.currentTx.status}:&nbsp;</strong> <a href={`https://${config.network === 'rinkeby' ? 'rinkeby.' : ''}etherscan.io/tx/${this.state.currentTx.txHash}`} target="_blank" rel="noreferrer noopener">{this.state.currentTx.txHash}</a>
          </div>}

        </div>
    )
  }
}



function showNoOfTokens() {
  if (!window.price) return;

  $('#noOfTokens').html(0+' '+config.tokenSymbol+' (1 ETH = '+window.price.toFixed(3)+' '+config.tokenSymbol+')')

  var noOfEthToSend = (1*$('#noOfEthToSendInput').val()).toFixed(15);
  if (isNaN(noOfEthToSend)||noOfEthToSend<=0) return;

  var noOfTokens = new BN(window.price).times(noOfEthToSend).toFixed(3).toString(10);
  $('#noOfTokens').html(noOfTokens+' '+config.tokenSymbol+' (1 ETH = '+window.price.toFixed(3)+' '+config.tokenSymbol+')')
  console.log(noOfTokens)
}

